

<template>
  <div id="app" :class="typeof weather.main != 'undefined' && weather.main.temp > 16 ?
        'warm' : '' ">
    <main>
      <div class="search-box">
        <input type="text"
               class="search-bar"
               placeholder="Search..."
               v-model="query"
               @keypress="fetchWeather">

      </div>
      <div class="error-box" v-if="weather.cod === '404'">
        <div class="error-message"><h1>Error: {{weather.cod}} Message: {{weather.message}}</h1></div>
      </div>
      <div class="weather-wrap" v-if="typeof weather.main !== 'undefined'">
        <div class="location-box">
          <div class="location">{{ weather.name }}, {{ weather.sys.country }}</div>
          <div class="date">{{ dateObj.dayName }}, {{ dateObj.date }} {{ dateObj.monthName }} {{ dateObj.year }}</div>
        </div>
        <div class="weather-box">
          <div class="temp">{{ Math.round(weather.main.temp) }}Â°c</div>
          <div class="humidity">Humidity {{ weather.main.humidity }}%</div>
          <div class="weather">{{ weather.weather[0].description }}</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>


export default {

  //https://openweathermap.org/current#name
  name: 'App',
  data() {
    return {
     // api_key: process.env.VUE_APP_API_KEY,
      url_base: "http://localhost:3000/",
      query: "",
      weather: {},
      weekDays: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
      months: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "Novemeber", "December"],
      dateObj: {}
    }
  },
  methods: {
    async fetchWeather(event) {
      if (event.key === "Enter") {

        let response = await fetch(`${this.url_base}?units=metric&q=${this.query}`);

        let json = await response.json()

        this.weather = await json;
        this.getCurrentDate();

      }
    },
    getCurrentDate() {
      let date = new Date();
      let day = date.getDay();
      let month = date.getMonth()

      this.dateObj = {
        dayName: this.weekDays[day],
        monthName: this.months[month],
        date: date.getDate(),
        year: date.getFullYear()
      }

    }
  }

}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montserrat", sans-serif;
}

#app {
  background-image: url("./assets/cold-bg.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4s;
}

#app.warm {
  background-image: url("./assets/warm-bg.jpg");
}

main {
  min-height: 100vh;
  padding: 25px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.75));
}

.search-box {
  width: 100%;
  margin-bottom: 30px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  color: #1f1f1f;
  font-size: 20px;

  appearance: none;
  border: none;
  outline: none;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
  background: rgba(255, 255, 255, 0.5);
  border-radius: 0px 16px 0px 16px;
  transition: 0.4s;

}

.search-box .search-bar:focus {
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
  background: rgba(255, 255, 255, 0.75);
  border-radius: 16px 0px 16px 0px;
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.25);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  font-style: italic;

}

.weather-box, .error-box{
  text-align: center;
}

.weather-box .temp {
  display: inline-block;
  padding: 10px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 16px;
  margin: 30px 0px;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.25);

}

.weather-box .humidity {
  color: #FFF;
  font-size: 60px;
  font-weight: 700;
  margin: 30px 0px;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  font-style: italic;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}



.error-box .error-message{
  color: #FFF;
  font-size: 30px;
  font-weight: 700;
  margin: 30px 0px;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}

</style>
